<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="6" v-for="(site,itemIndex) in vueRecommend" :key="itemIndex">
        <el-card shadow="hover" class="box-card">
          <a :href="site.siteUrl">
            <img :src="site.siteIcon" class="card-icon">
          </a>
          <div class="site-content">
            <div class="site-title">
              {{site.siteTitle}}
            </div>
            <div class="site-description">
              {{site.siteDescription}}
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'MainContent',
  data() {
    return {
      vueRecommend: [
        {
          siteIcon: require("@/assets/img/nav_menu/angular.png"),
          siteTitle: 'Angular.js官网',
          siteDescription: 'Angular.js的中文官网',
          siteUrl: "https://angular.cn/"
        },
      ]
    }
  },
}
</script>

<style>
.card-icon {
  height: 40px;
  width: 40px;
  float: left;
  margin-right: 10px;
}

.box-card {
  border-radius: 12px;
  height: 90px;
  margin-top: 15px;
  /* display: flex; */
}

.site-title {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}

.site-description {
  color: #979898;
  font-size: 13px;
  margin: 2px;
  word-break: break-word;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
</style>


